import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/Serveis',
    name: 'serveis',
    component: () => import('../views/Serveis.vue')
  },
  {
    path: '/PacksiPreus',
    name: 'packsipreus',
    component: () => import('../views/PacksiPreus.vue')
  },
  {
    path: '/BlogClients',
    name: 'blogclients',
    component: () => import('../views/BlogClients.vue')
  },
  {
    path: '/Contacte',
    name: 'contacte',
    component: () => import('../views/Contacte.vue')
  }
];

const router = new VueRouter({
  mode: 'history',
  base: '/practica/',
  routes
});

export default router;
